export default {
	data () {
		return {
			// 用户选择
			 anchorIndex:1,
			 perIndex:1,
		}
	},
	methods: {
		//点击按钮变色
		btnClick(e){
			if(e.target.dataset.index != 2 ){
			    this.anchorIndex = 1;
			}else{
			    //获取当前点击事件的下标
			    this.anchorIndex =  e.target.dataset.index
			}
		},
		perClick(e) {
			if(e.target.dataset.index != 2){
				this.perIndex = 1;
			} else {
				this.perIndex = e.target.dataset.index
			}
		}
	}
}
